import { SessionStorageConfig, WindowSessionData } from "./types";

export class SessionStorageController {
    private sessionStorage: Storage = window.sessionStorage;
    private windowId!: string;
    private get allNamespaces(): { namespace: string; defaultValue: any }[] {
        return [{ namespace: this.windowNamespace, defaultValue: {} }];
    }

    public configure(config: SessionStorageConfig): void {
        this.windowId = config.windowId;

        this.allNamespaces.forEach(({ namespace, defaultValue }) => {
            const data = this.sessionStorage.getItem(namespace);

            if (!data) {
                this.sessionStorage.setItem(namespace, JSON.stringify(defaultValue));
            }
        });
    }

    private get windowNamespace(): string {
        return `io_connect_window_${this.windowId}`;
    }

    public getWindowData(): WindowSessionData {
        return JSON.parse(this.sessionStorage.getItem(this.windowNamespace) as string);
    }

    public setWindowData<Key extends keyof WindowSessionData>(data: WindowSessionData[Key], key: Key): void {
        const allData = this.getWindowData();

        allData[key] = data;

        this.sessionStorage.setItem(this.windowNamespace, JSON.stringify(allData));
    }
}
