import { IOConnectDesktop } from "../../desktop";
import { ThemesImpl } from "./themes";

export function factory(contexts: IOConnectDesktop.Contexts.API, interop: IOConnectDesktop.Interop.API): IOConnectDesktop.Themes.API & { ready: () => Promise<void>; } {
    const themes = new ThemesImpl(contexts, interop);

    return {
        list: themes.list.bind(themes),
        getCurrent: themes.getCurrent.bind(themes),
        select: themes.select.bind(themes),
        onChanged: themes.onChanged.bind(themes),
        ready: () => Promise.resolve(),
    };
}
