import { channelContextDecoder, getMyChanelResultDecoder, getWindowIdsOnChannelDataDecoder, getWindowIdsOnChannelResultDecoder, getWindowIdsWithChannelsResultDecoder, joinChannelDataDecoder, wrappedWindowWithChannelFilterDecoder } from "../shared/decoders";
import { BridgeOperation } from "../shared/types";
import { IOConnectBrowser } from "../../browser";

export type ChannelOperationTypes = "addChannel" | "getMyChannel" | "getWindowIdsOnChannel" | "getWindowIdsWithChannels" | "joinChannel";

export const operations: { [key in ChannelOperationTypes]: BridgeOperation } = {
    addChannel: { name: "addChannel", dataDecoder: channelContextDecoder },
    getMyChannel: { name: "getMyChannel", resultDecoder: getMyChanelResultDecoder },
    getWindowIdsOnChannel: { name: "getWindowIdsOnChannel", dataDecoder: getWindowIdsOnChannelDataDecoder, resultDecoder: getWindowIdsOnChannelResultDecoder },
    getWindowIdsWithChannels: { name: "getWindowIdsWithChannels", dataDecoder: wrappedWindowWithChannelFilterDecoder, resultDecoder: getWindowIdsWithChannelsResultDecoder },
    joinChannel: { name: "joinChannel", dataDecoder: joinChannelDataDecoder },
};

export interface MyChannelData {
    channel?: string;
}

export interface WindowIdsOnChannelData {
    windowIds: string[];
}

export interface WindowIdsWithChannelsData {
    windowIdsWithChannels: {
        application: string;
        channel?: string;
        windowId: string;
    }[]
}

export interface WrappedWindowWithChannelFilter {
    filter?: IOConnectBrowser.Channels.WindowWithChannelFilter
}

export interface JoinChannelData {
    channel: string;
    windowId: string;
}
