import React, { createContext, memo } from 'react';
import { node, object } from 'prop-types';
import { IOConnectBrowser } from '@interopio/browser';
import { IOConnectDesktop } from '@interopio/desktop';
import { useIOConnectInit } from './useGlueInit';
import { IOConnectProviderProps } from '../react-hooks';

export const IOConnectContext = createContext<IOConnectBrowser.API | IOConnectDesktop.API>(null);

export const IOConnectProvider: React.FC<IOConnectProviderProps> = memo(
    ({ children, fallback = null, settings = {} }) => {
        
        const glue = useIOConnectInit(settings);

        return glue ? (
            <IOConnectContext.Provider value={glue}>{children}</IOConnectContext.Provider>
        ) : (
            <>{fallback}</>
        );
    }
);

IOConnectProvider.propTypes = {
    children: node,
    settings: object,
    fallback: node,
};

IOConnectProvider.displayName = 'IOConnectProvider';
