/* eslint-disable @typescript-eslint/no-explicit-any */
import { UnsubscribeFunction } from "callback-registry";
import { IOConnectBrowser } from "../../browser";
import { AppManagerController } from "./controller";
import { BaseApplicationData } from "./protocol";
import { startApplicationContextDecoder, startApplicationOptionsDecoder } from "../shared/decoders";

export class ApplicationModel {
    private me!: IOConnectBrowser.AppManager.Application;

    constructor(
        private readonly data: BaseApplicationData,
        private readonly instances: IOConnectBrowser.AppManager.Instance[],
        private readonly controller: AppManagerController,
    ) { }

    public toApi(): IOConnectBrowser.AppManager.Application {
        const api: IOConnectBrowser.AppManager.Application = {
            name: this.data.name,
            title: this.data.title as string,
            version: this.data.version as string,
            icon: this.data.icon as string,
            caption: this.data.caption as string,
            userProperties: this.data.userProperties,
            instances: this.instances,
            start: this.start.bind(this),
            onInstanceStarted: this.onInstanceStarted.bind(this),
            onInstanceStopped: this.onInstanceStopped.bind(this)
        };

        this.me = api;

        return this.me;
    }

    private onInstanceStarted(callback: (instance: IOConnectBrowser.AppManager.Instance) => any): UnsubscribeFunction {

        if (typeof callback !== "function") {
            throw new Error("OnInstanceStarted requires a single argument of type function");
        }

        return this.controller.onInstanceStarted((instance) => {
            if (instance.application.name === this.data.name) {
                callback(instance);
            }
        });
    }

    private onInstanceStopped(callback: (instance: IOConnectBrowser.AppManager.Instance) => any): UnsubscribeFunction {
        if (typeof callback !== "function") {
            throw new Error("OnInstanceStarted requires a single argument of type function");
        }

        return this.controller.onInstanceStopped((instance) => {
            if (instance.application.name === this.data.name) {
                callback(instance);
            }
        });
    }

    private async start(context?: any, options?: IOConnectBrowser.AppManager.ApplicationStartOptions): Promise<IOConnectBrowser.AppManager.Instance> {
        const verifiedContext = startApplicationContextDecoder.runWithException(context);
        const verifiedOptions = startApplicationOptionsDecoder.runWithException(options);

        return this.controller.startApplication(this.data.name, verifiedContext, verifiedOptions);
    }
}