import CreateGlue from "./glue";

let whatToExpose = CreateGlue;
let shouldSetToWindow = true;
if (typeof window !== "undefined") {
    const windowAsAny = window as any;
    // if iodesktop (running in GD3) and autoInjected
    const iodesktop = windowAsAny.iodesktop ?? windowAsAny.glue42gd;
    if (iodesktop && iodesktop.autoInjected) {
        whatToExpose = windowAsAny.IODesktop ?? windowAsAny.Glue;
        // no need to set to window, it's already there
        shouldSetToWindow = false;
    }

    if (shouldSetToWindow) {
        windowAsAny.Glue = whatToExpose; // to keep backward compatibility
        windowAsAny.IODesktop = whatToExpose;
    }

    // remove IOBrowser && GlueCore from the window object (injected by Glue-core)
    delete windowAsAny.IOBrowser;
    delete windowAsAny.GlueCore;
}
// add default library for ES6 modules
(whatToExpose as any).default = whatToExpose;

export default whatToExpose;
