/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* tslint:disable:no-console no-empty */
import { IOConnectCoreFactoryFunction } from "@interopio/core";
import { IOConnectDesktop } from "@interopio/desktop";
import { IOConnectBrowser, IOConnectBrowserFactoryFunction } from "../browser";
import { parseConfig } from "./config";
import { checkSingleton } from "./config/checkSingleton";
import { enterprise } from "./enterprise";
import { IoC } from "./shared/ioc";
import { PromiseWrap } from "./shared/promise-plus";
import { version } from "../package.json";

/** This function creates the factory function which is the default export of the library */
export const createFactoryFunction = (coreFactoryFunction: IOConnectCoreFactoryFunction): IOConnectBrowserFactoryFunction => {

    return async (userConfig?: IOConnectBrowser.Config): Promise<IOConnectBrowser.API | IOConnectDesktop.API> => {
        if (window.glue42gd || (window as any).iodesktop) {
            return enterprise(userConfig);
        }

        const ioc = new IoC();

        const config = parseConfig(userConfig);

        checkSingleton();

        const glue = await PromiseWrap<IOConnectBrowser.API>(() => coreFactoryFunction(config, { version }) as Promise<IOConnectBrowser.API>, 30000, "Glue Web initialization timed out, because core didn't resolve");

        const logger = glue.logger.subLogger("web.main.controller");

        ioc.defineGlue(glue);

        ioc.sessionController.configure({ windowId: glue.interop.instance.instance as string });

        await ioc.preferredConnectionController.start(config);

        await ioc.bridge.start(ioc.controllers);

        ioc.defineConfig(config);

        logger.trace("the bridge has been started, initializing all controllers");

        await Promise.all(Object.values(ioc.controllers).map((controller) => controller.start(glue, ioc)));

        logger.trace("all controllers reported started, starting all additional libraries");

        await Promise.all(config.libraries.map((lib: any) => lib(glue, config)));

        logger.trace("all libraries were started");

        ioc.eventsDispatcher.start(glue);

        logger.trace("start event dispatched, glue is ready, returning it");

        return glue;
    };
};
