import { IOConnectDesktop } from "../../../desktop";
import jumpListManager from "./manager";

export default class JumpListActions implements IOConnectDesktop.Windows.JumpListActionsAPI {

    private _categoryTitle: string;

    constructor(private windowId: string, configuration: IOConnectDesktop.Windows.JumpListCategorySettings) {
        this._categoryTitle = configuration.title;
    }

    public list(): Promise<IOConnectDesktop.Windows.JumpListActionSettings[]> {
        return jumpListManager.getActions(this.windowId, this._categoryTitle);
    }

    public create(actions: IOConnectDesktop.Windows.JumpListActionSettings[]): Promise<void> {
        return jumpListManager.createActions(this.windowId, this._categoryTitle, actions);
    }

    public remove(actions: IOConnectDesktop.Windows.JumpListActionSettings[]): Promise<void> {
        return jumpListManager.removeActions(this.windowId, this._categoryTitle, actions);
    }
}
