import { ENTERPRISE_NO_APP_NAME, GLUE42_FDC3_INTENTS_METHOD_PREFIX } from "../shared/constants";
import { StartContextDecoder } from "../shared/decoders";
import { validateGlue, extractDecoderError } from "../shared/utils";
import { Application, IOConnect, Intent, IntentHandler, IntentInfo, IntentRequest, InteropMethod, InvocationResult, HandlerFilter, ServerInstance, UnsubscribeFunction, StartupContext } from "../types/glue";
import { BuildRemovedIntentHandlerArgs, ResolverIntentHandler, ResolvingMethod } from "../types/types";

export class GlueController {
    private glue!: IOConnect;

    private _intent?: string | IntentRequest;
    private _handlerFilter?: HandlerFilter;
    private _callerId!: string;
    private _methodName!: string;
    private _resolvingMethod!: ResolvingMethod;

    public async initialize(glue: IOConnect): Promise<void> {
        const { ok, error } = validateGlue(glue);

        if (!ok) {
            return this.announceInitFailure(error as string);
        }

        this.glue = glue;

        const context = await this.glue.windows.my().getContext();

        await this.validateStartupContext(context);

        this._intent = context.intent;
        this._callerId = context.callerId;
        this._methodName = context.methodName;
        this._handlerFilter = context.handlerFilter;

        this._resolvingMethod = context.handlerFilter ? "filterHandlers" : "raise";
    }

    public get intent(): string | IntentRequest | undefined {
        return this._intent;
    }

    public get resolvingMethod(): ResolvingMethod {
        return this._resolvingMethod;
    }

    public get handlerFilter(): HandlerFilter | undefined {
        return this._handlerFilter;
    }

    public getTitle(): string | undefined {
        if (this.resolvingMethod === "filterHandlers") {
            return this.handlerFilter!.title;
        }

        return `Choose an app to ${this.getIntentName()}`;
    }

    public async sendInteropMethodResponse(handler: ResolverIntentHandler): Promise<InvocationResult | undefined> {
        const extendedHandler = this.resolvingMethod === "raise"
            ? await this.getIntentHandlerForRaise(handler)
            : await this.getIntentHandlerForFilterHandlers(handler);

        if (!extendedHandler) {
            throw new Error(`There's no such existing intent handler: ${JSON.stringify(handler)}`);
        }

        const methodIsRegistered = this.isMethodRegistered();

        if (!this.isInstanceStillRunning()) {
            this.glue.windows.my().close();
            return;
        }

        if (!methodIsRegistered) {
            return this.waitForResponseMethodAdded(extendedHandler);
        }

        return this.glue.interop.invoke(this._methodName, { intent: this.getIntentName(), handler: extendedHandler }, { instance: this._callerId });
    }

    public subscribeForServerMethodAdded(callback: (handler: ResolverIntentHandler) => void): UnsubscribeFunction {
        return this.glue.interop.serverMethodAdded(({ server, method }) => {
            const isInstanceHandler = this.checkIfInstanceIntentHandler(server, method);

            if (!isInstanceHandler) {
                return;
            }

            this.buildAddedIntentHandler(server.applicationName, server.instance)
                .then(builtInstance => {
                    if (!builtInstance) {
                        return;
                    }

                    callback(builtInstance);
                });
        });
    }

    public subscribeForServerMethodRemoved(callback: (handler: ResolverIntentHandler) => void): UnsubscribeFunction {
        return this.glue.interop.serverMethodRemoved(({ server, method }) => {
            const isInstanceHandler = this.checkIfInstanceIntentHandler(server, method);

            if (!isInstanceHandler) {
                return;
            }

            const builtInstance = this.buildRemovedIntentHandler({ instance: server });

            callback(builtInstance);
        });
    }

    public subscribeOnAppAdded(callback: (handler: ResolverIntentHandler) => void): UnsubscribeFunction {
        return this.glue.appManager.onAppAdded((app: Application) => {

            this.checkIfAppIntentHandler(app, "added")
                .then((isIntentHandler) => {

                    if (!isIntentHandler) {
                        return;
                    }

                    return this.buildAddedIntentHandler(app.name);

                })
                .then((builtHandler) => {
                    if (!builtHandler) {
                        return;
                    }

                    callback(builtHandler);
                });
        });
    }

    public subscribeOnAppRemoved(callback: (handler: ResolverIntentHandler) => void): UnsubscribeFunction {
        return this.glue.appManager.onAppRemoved((app: Application) => {

            this.checkIfAppIntentHandler(app, "removed")
                .then((isIntentHandler) => {

                    if (!isIntentHandler) {
                        return;
                    }

                    return this.buildRemovedIntentHandler({ app });

                })
                .then((builtHandler) => {
                    if (!builtHandler) {
                        return;
                    }

                    callback(builtHandler);
                });
        });
    }

    private getIntentName(): string | undefined {
        return typeof this.intent === "object" ? this.intent.intent : this.intent;
    }

    private waitForResponseMethodAdded(handler: ResolverIntentHandler): Promise<InvocationResult> {
        const responsePromise: Promise<InvocationResult> = new Promise((resolve, reject) => {
            const unsub = this.glue.interop.serverMethodAdded(({ server, method }) => {
                if (server.instance !== this._callerId || method.name !== this._methodName) {
                    return;
                }

                this.glue.interop.invoke(this._methodName, { intent: this.intent, handler }, { instance: this._callerId })
                    .then((invocationRes) => {
                        resolve(invocationRes);
                    })
                    .catch((error) => {
                        reject(error);
                    })
                    .finally(() => {
                        if (unsub) {
                            unsub();
                        }
                    });
            });
        });

        return responsePromise;
    }

    private async checkIfAppIntentHandler(app: Application, method: "added" | "removed"): Promise<boolean> {
        if (this.resolvingMethod === "raise" && typeof this.intent === "object" && this.intent.handlers) {
            return this.checkIfAppInPassedHandlers(app);
        }

        const globalBrowserNamespace = (window as any).glue42core || (window as any).iobrowser;

        if (globalBrowserNamespace && globalBrowserNamespace.webStarted) {
            return this.checkInBrowserIfAppShouldShow(app, method);
        }

        if ((window as any).glue42gd) {
            return this.checkInDesktopIfAppShouldShow(app);
        }

        return false;
    }

    private checkIfInstanceIntentHandler(server: ServerInstance, method: InteropMethod): boolean {
        if (this.resolvingMethod === "filterHandlers") {
            return this.checkIfValidIntentMethodWithHandlerFilter(method);
        }

        const isMethodForCurrentIntent = this.isMethodIsForPassedIntent(method.name, this.getIntentName()!);

        if (!isMethodForCurrentIntent) {
            return false;
        }

        if (typeof this.intent === "object" && this.intent.handlers && !this.checkIfInstanceInPassedHandlers(server)) {
            return false;
        }

        return true;
    }

    private checkIfValidIntentMethodWithHandlerFilter(method: InteropMethod): boolean {
        if (this.handlerFilter?.intent && !this.isMethodIsForPassedIntent(method.name, this.handlerFilter.intent)) {
            return false;
        }
        
        const intentFlag = method.flags.intent;

        if (this.handlerFilter?.resultType && (!intentFlag || !intentFlag.resultType || intentFlag.resultType !== this.handlerFilter.resultType)) {
            return false;
        }

        if (this.handlerFilter?.contextTypes && (!intentFlag || !intentFlag.contextTypes || !intentFlag.contextTypes.length || !this.handlerFilter.contextTypes.every((contextType) => intentFlag.contextTypes.includes(contextType)))) {
            return false;
        }

        return true;
    }

    private checkIfAppInPassedHandlers(app: Application): boolean {
        const handlersForApp = (this.intent as IntentRequest).handlers?.filter(handler => handler.applicationName === app.name);

        if (!handlersForApp?.length) return false;

        if ((this.intent as IntentRequest).target === "startNew") {
            return !!handlersForApp.find((handler) => handler.type === "app");
        }

        if ((this.intent as IntentRequest).target === "reuse") {
            const openedInstance = handlersForApp.find(handler => handler.type === "instance");

            // show the app ONLY if there's no opened instance 
            return !openedInstance;
        }

        return !!handlersForApp.find(handler => handler.type === "app");
    }

    private async checkInBrowserIfAppShouldShow(app: Application, method: "added" | "removed"): Promise<boolean> {
        if (this.resolvingMethod === "filterHandlers") {
            return this.isThereValidIntentHandlerByFilter(app.userProperties.intents);
        }
        
        const intentName = this.getIntentName();

        if (method === "removed") {
            return !!app.userProperties.intents?.find((intent: Intent) => intent.name === intentName);
        }
        
        // show instances and apps which do NOT have opened instances
        if (typeof this.intent === "object" && (!this.intent.target || this.intent.target === "reuse")) {
            // check if app registers the intent in its app definition
            const currentIntent = app.userProperties.intents?.find((intent: Intent) => intent.name === intentName);

            if (!currentIntent) return false;

            const intent = (await this.glue.intents.find(intentName)).find(intent => intent.name === intentName);

            if (!intent) return false;

            return !intent.handlers.find(handler => handler.applicationName === app.name && handler.type === "instance" && handler.instanceId);
        }

        return !!app.userProperties.intents?.find((intent: Intent) => intent.name === intentName);
    }

    private async checkInDesktopIfAppShouldShow(app: Application): Promise<boolean> {
        if (this.resolvingMethod === "filterHandlers") {
            const allIntents = await this.glue.intents.all();

            // filter intents and handlers only for current app
            const intentsForCurrentApp = allIntents.map((intent) => {
                const currentAppHandlers = intent.handlers.filter((handler) => handler.applicationName === app.name && handler.type === "app");

                if (!currentAppHandlers.length) {
                    return;
                }

                intent.handlers = currentAppHandlers;

                return intent;
            }).filter((intentWithUpdatedHandlers) => intentWithUpdatedHandlers && intentWithUpdatedHandlers.handlers.length);

            return intentsForCurrentApp.length ? this.isThereValidIntentHandlerByFilter(intentsForCurrentApp as Intent[]) : false;
        }
        
        const intentName = this.getIntentName();

        const intent = (await this.glue.intents.find(intentName)).find(intent => intent.name === intentName);

        if (!intent) return false;

        if (typeof this.intent === "object" && (!this.intent.target || this.intent.target === "reuse")) {
            const handlersForCurrentIntent = intent.handlers.filter(handler => handler.applicationName === app.name);

            if (!handlersForCurrentIntent.length) return false;

            const openedInstance = handlersForCurrentIntent.some(handler => handler.instanceId);

            return !openedInstance;
        }

        return !!intent.handlers.find((handler) => handler.applicationName === app.name);
    }

    private checkIfInstanceInPassedHandlers(server: ServerInstance): boolean {
        return !!(this.intent as IntentRequest).handlers?.find(handler => handler.applicationName === server.application && server.instance === handler.instanceId);
    }

    private isThereValidIntentHandlerByFilter(intents: IntentInfo[]): boolean {
        return intents?.some((intentInfo) => {
            if (this.handlerFilter?.intent && this.handlerFilter.intent !== intentInfo.name) {
                return;
            }
            
            if (this.handlerFilter?.resultType && this.handlerFilter?.resultType !== intentInfo.resultType) {
                return;
            }

            if (this.handlerFilter?.contextTypes && (!intentInfo.contexts || !this.handlerFilter.contextTypes.every((contextType) => intentInfo.contexts?.includes(contextType)))) {
                return;
            }

            return true;
        });
    }

    private async buildAddedIntentHandler(appName: string, instanceId?: string): Promise<ResolverIntentHandler | undefined> {
        const foundApp = this.glue.appManager.application(appName);

        const serverInstance = this.glue.interop.servers().find(server => server.instance === instanceId);

        if (!foundApp && !serverInstance) {
            return;
        }

        // iframe will no be found as an app but it can still be a valid handler
        if (foundApp) {
            /* Handle cases where a window is opened via glue.windows.open() and it adds a intent listener for the current intent:
                Glue42 Core -> there will be no application in AppManager API
                Glue42 Enterprise -> there will be an instance of an application called "no-app-window" in AppManager API 
            */
            return {
                applicationName: foundApp.name !== ENTERPRISE_NO_APP_NAME ? foundApp.name : serverInstance?.applicationName || serverInstance?.application || "",
                applicationTitle: serverInstance ? await this.getHandlerTitle(serverInstance) : foundApp.title || instanceId,
                applicationIcon: foundApp?.icon,
                instanceId
            };
        }

        return {
            applicationName: serverInstance!.applicationName || serverInstance!.application || serverInstance!.instance || "",
            applicationTitle: await this.getHandlerTitle(serverInstance!),
            applicationIcon: "",
            instanceId
        };
    }

    private buildRemovedIntentHandler({ instance, app }: BuildRemovedIntentHandlerArgs): ResolverIntentHandler {
        if (instance) {
            return {
                applicationName: instance.applicationName || instance.application || "",
                instanceId: instance.instance || instance.windowId
            };
        }

        return {
            applicationName: app!.name,
            applicationTitle: app!.title,
            applicationIcon: app!.icon,
        };
    }

    private async getHandlerTitle(server: ServerInstance): Promise<string> {
        if (!server.windowId) {
            return server.applicationName || server.application || server.instance || "";
        }

        const winDef = this.glue.windows.findById(server.windowId);

        const serverInstanceInfo = server.applicationName || server.application || server.instance || "";

        if (!winDef) {
            return serverInstanceInfo;
        }

        const title = await winDef.getTitle();

        return title || serverInstanceInfo;
    }

    private async getIntentHandlerForRaise(userHandler: ResolverIntentHandler): Promise<IntentHandler | undefined> {
        const intentName = this.getIntentName();

        const searchedIntent = (await this.glue.intents.find(intentName)).find(intent => intent.name === intentName);

        if (!searchedIntent) {
            throw new Error(`Intent with name ${intentName} does not exist`);
        }

        const handler = this.findHandlerByFilter({ instanceId: userHandler.instanceId, applicationName: userHandler.applicationName }, searchedIntent.handlers);

        return handler;
    }

    private async getIntentHandlerForFilterHandlers(userHandler: ResolverIntentHandler): Promise<IntentHandler | undefined> {
        const allIntents = await this.glue.intents.all();

        const allIntentHandlers = allIntents.map((intent) => intent.handlers).flat(1);

        const handler = this.findHandlerByFilter({ instanceId: userHandler.instanceId, applicationName: userHandler.applicationName }, allIntentHandlers);

        return handler;
    }

    private findHandlerByFilter(filterObject: { instanceId?: string, applicationName?: string }, handlers: IntentHandler[]): IntentHandler | undefined {
        if (filterObject.instanceId) {
            return handlers.find(handler => handler.instanceId === filterObject.instanceId);
        }

        if (filterObject.applicationName) {
            return handlers.find(handler => handler.applicationName === filterObject.applicationName);
        }
    }

    private isMethodRegistered(): boolean {
        return !!this.glue.interop.methods(this._methodName).length;
    }

    private isInstanceStillRunning(): boolean {
        return !!this.glue.interop.servers().find(server => server.instance === this._callerId);
    }

    private isMethodIsForPassedIntent(methodName: string, intentName: string): boolean {
        const expectedMethodName = `${GLUE42_FDC3_INTENTS_METHOD_PREFIX}${intentName}`;

        return expectedMethodName === methodName;
    }

    private async validateStartupContext(context: StartupContext): Promise<void> {
        const decoderRes = StartContextDecoder.run(context);

        if (!decoderRes.ok) {
            const errorMsg = extractDecoderError(decoderRes.error);

            return this.announceInitFailure(errorMsg, context);
        }

        const baseErrorMsg = `Invalid startup context: ${JSON.stringify(context)}`;

        if (!context.intent && !context.handlerFilter) {
            return this.announceInitFailure(`${baseErrorMsg}. Either 'intent' or 'handlerFilter' should be present.`, context);
        }

        if (!context.handlerFilter) {
            return;
        }

        const { intent, resultType, contextTypes } = context.handlerFilter;

        if (!Object.keys(context.handlerFilter).length || (!intent && !resultType && (!contextTypes || !contextTypes.length))) {
            return this.announceInitFailure(`Invalid startup context: ${JSON.stringify(context)}. Provide at least one filter criteria of the following: 'contextTypes' | 'intent' | 'resultType'`, context);
        }
    }

    private announceInitFailure(errorMsg: string, context?: StartupContext): Promise<void> {
        return new Promise((res, rej) => {
            if (!this.glue || !this.glue.interop || !context || !context.methodName || typeof context.methodName !== "string" || !context.callerId || typeof context.callerId !== "string") {
                rej(errorMsg);
            }
            
            this.glue.interop.invoke(context!.methodName, { error: errorMsg }, { instance: context!.callerId }).then(() => res()).catch(() => rej());
        });
    }
}
