import { IOConnectDesktop } from "../../desktop";

// provides a unified way to access layouts array
export class LayoutStore {
    private layouts: IOConnectDesktop.Layouts.Layout[] = [];

    public removeWhere(condition: (l: IOConnectDesktop.Layouts.Layout) => boolean) {
        this.layouts = this.layouts.filter(condition);
    }

    public removeAll() {
        this.layouts = [];
    }

    public add(item: IOConnectDesktop.Layouts.Layout) {
        this.layouts.push(item);
    }

    public get all(): IOConnectDesktop.Layouts.Layout[] {
        return this.layouts;
    }

    public where(condition: (l: IOConnectDesktop.Layouts.Layout) => boolean): IOConnectDesktop.Layouts.Layout[] {
        return this.layouts.filter(condition);
    }

    public first(condition: (l: IOConnectDesktop.Layouts.Layout) => boolean): IOConnectDesktop.Layouts.Layout {
        return this.where(condition)[0];
    }
}

export default new LayoutStore();
