import { IOConnectDesktop } from "../../desktop";
import { Cookies } from "./index";

export function factory(agm: IOConnectDesktop.AGM.API, methodName: string): IOConnectDesktop.Cookies.API & { ready: () => Promise<void>; } {
    const cookies = new Cookies(methodName, agm);

    return {
        get: cookies.get.bind(cookies),
        remove: cookies.remove.bind(cookies),
        set: cookies.set.bind(cookies),
        ready: () => Promise.resolve()
    };
}
