import { IOConnectDesktop } from "../desktop";

interface CallDetails {
    date: Date;
    config: IOConnectDesktop.Config;
}

export class FactoryCallInfo {

    public promise: Promise<IOConnectDesktop.API>;
    public initialized = false;
    public details: CallDetails[] = [];
    public reject: (reason?: any) => void = () => { /** Do nothing - default impl. - will be replaced */ };
    public resolve: (value: IOConnectDesktop.API | PromiseLike<IOConnectDesktop.API>) => void = () => { /** Do nothing - default impl. - will be replaced */ };

    public init(config: IOConnectDesktop.Config) {
        this.initialized = true;
        this.addCall(config);
        this.promise = new Promise((resolve, reject) => {
            this.resolve = resolve;
            this.reject = reject;
        });
    }

    public addCall(config: IOConnectDesktop.Config) {
        this.details.push({ date: new Date(), config });
    }

    public done(g: IOConnectDesktop.API) {
        this.resolve(g);
    }

    public error(e: Error) {
        this.reject(e);
    }
}
