    public async activate(w: IOConnectDesktop.Windows.IOConnectWindow): Promise<IOConnectDesktop.Windows.IOConnectWindow> {
        let un: UnsubscribeFunction;
        try {
            const done = new Promise<void>((resolve, reject) => {
                un = w.onFocusChanged(() => {
                    resolve();
                });
            });
            await Promise.all([this.execute("activate", { windowId: w.id }, "FocusChanged"), done]);
            return w;
        } catch (error) {
            throw error;
        } finally {
            if (un) {
                un();
            }
        }
    }

