    public async detachTab(w: IOConnectDesktop.Windows.IOConnectWindow, options: DetachTabOptions): Promise<IOConnectDesktop.Windows.IOConnectWindow> {
        const eventKeys: string[] = ["WindowFrameRemoved", `WindowFrameAdded`];
        if (!isUndefinedOrNull(options?.relativeTo)) {
            eventKeys.push(`CompositionChanged`);
            eventKeys.push(`CompositionChanged-${options.relativeTo}`);
        } else {
            eventKeys.push("BoundsChanged");
        }
        await this.execute("detachTab", { windowId: w.id, options }, ...eventKeys);
        return w;
    }

