    public async updateContext(w: IOConnectDesktop.Windows.IOConnectWindow, context: any, replace: boolean): Promise<IOConnectDesktop.Windows.IOConnectWindow> {
        let un: UnsubscribeFunction;
        try {
            // as undefined values do not go through interop, setting a prop to undefined will lead to no change
            // set this to null so it actually removes the prop
            const contextWithoutUndefinedValues = this.swapUndefinedToNull(context);

            const done = new Promise<void>((resolve, reject) => {
                un = w.onContextUpdated(() => {
                    resolve();
                });
            });
            await Promise.all([this.execute("updateContext", {
                windowId: w.id, context: contextWithoutUndefinedValues, replace
            }), done]);
            return w;
        } catch (error) {
            throw error;
        } finally {
            if (un) {
                un();
            }
        }
    }

