import { useEffect, useState } from "react";
import { UseIOInitFunc } from "../react-hooks";

export const useIOConnectInit: UseIOInitFunc = (settings) => {
    const [io, setIOConnect] = useState(null);
    useEffect(() => {
        const initialize = async () => {
            try {
                if (settings.browser && settings.browserPlatform) {
                    throw new Error("Cannot initialize, because the settings are over-specified: defined are both browser and browserPlatform. Please set one or the other");
                }

                const isDesktop = (typeof (window as any).glue42gd !== "undefined") || (typeof (window as any).iodesktop !== "undefined");

                if (isDesktop) {
                    const factory = settings.desktop?.factory || settings.browser?.factory || settings.browserPlatform?.factory || (window as any).Glue;
                    const config = settings.desktop?.config || settings.browser?.config || settings.browserPlatform?.config;

                    const factoryResult = await factory(config);

                    setIOConnect(factoryResult.io || factoryResult.glue || factoryResult);
                    return;
                }

                const config = settings.browser?.config || settings.browserPlatform?.config;
                const factory = settings.browser?.factory || settings.browserPlatform?.factory || (window as any).IOBrowser || (window as any).IOBrowserPlatform;

                const factoryResult = await factory(config);

                setIOConnect(factoryResult.io || factoryResult.glue || factoryResult);
            } catch (e) {
                console.error(e);
            }
        };
        initialize();
    }, []);
    return io;
};
