import { IOConnectDesktop } from "../../../desktop";
import JumpListCategories from "./categories";
import jumpListManager from "./manager";

export default class JumpList implements IOConnectDesktop.Windows.JumpList {

    private _categories: IOConnectDesktop.Windows.JumpListCategoriesAPI;

    constructor(private windowId: string) {
        this._categories = new JumpListCategories(windowId);
    }

    get categories(): IOConnectDesktop.Windows.JumpListCategoriesAPI {
        return this._categories;
    }

    public async isEnabled(): Promise<boolean> {
        const configuration = await jumpListManager.getJumpListSettings(this.windowId);
        return configuration.enabled;
    }

    public setEnabled(enabled: boolean): Promise<void> {
        return jumpListManager.setEnabled(this.windowId, enabled);
    }
}
