import { IoC } from "./shared/ioc";
import { IOConnect } from "./types/glue";
import { IntentsResolverFactoryFunction } from "./types/types";

const factoryFunction: IntentsResolverFactoryFunction = async (io: IOConnect): Promise<void> => {
    const ioc = new IoC();

    try {
        await ioc.glueController.initialize(io);
    } catch (error) {
        throw new Error(`The Intents Resolver API did not initialized successfully. Error: ${JSON.stringify(error)}`);
    }

    io.intents.resolver = ioc.intentsResolver;

    Object.freeze(io.intents);
};

// attach to window
if (typeof window !== "undefined") {
    /* eslint-disable @typescript-eslint/no-explicit-any */
    (window as any).IOConnectIntentsResolver = factoryFunction;
}

export default factoryFunction;
