import { IOConnectDesktop } from "../../../desktop";
import JumpListActions from "./actions";
import jumpListManager from "./manager";

export default class JumpListCategories implements IOConnectDesktop.Windows.JumpListCategoriesAPI {

    constructor(private windowId: string) {
    }

    public list(): Promise<IOConnectDesktop.Windows.JumpListCategory[]> {
        return this.getCategories();
    }

    public create(title: string, actions: IOConnectDesktop.Windows.JumpListActionSettings[]): Promise<void> {
        return jumpListManager.createCategory(this.windowId, title, actions);
    }

    public remove(title: string): Promise<void> {
        return jumpListManager.removeCategory(this.windowId, title);
    }

    public async find(title: string): Promise<IOConnectDesktop.Windows.JumpListCategory> {
        const categories = await this.getCategories();
        return categories.find((cat) => cat.title === title);
    }

    private async getCategories(): Promise<IOConnectDesktop.Windows.JumpListCategory[]> {
        const result: IOConnectDesktop.Windows.JumpListCategory[] = [];

        const configuration = await jumpListManager.getJumpListSettings(this.windowId);
        configuration.categories.forEach((category) => {
            result.push({
                title: category.title,
                actions: new JumpListActions(this.windowId, category)
            });
        });
        return result;
    }
}
