import { appManagerCompulsoryProps, glueCompulsoryProps } from "./constants";
import { DecoderError } from "decoder-validate";

export const validateGlue = (glue: any): { ok: boolean, error?: string } => {
    const glueAPIs: string[] = Object.keys(glue);

    const missingAPIs = glueCompulsoryProps.filter(prop => !glueAPIs.includes(prop));

    if (missingAPIs.length) {
        return { ok: false, error: `Glue is missing the following API${missingAPIs.length > 1 ? "s" : ""}: ${missingAPIs.join(", ")}` };
    }

    const appManagerProps = Object.keys(glue.appManager);

    const missingAppManagerProps = appManagerCompulsoryProps.filter(prop => !appManagerProps.includes(prop));

    if (!missingAppManagerProps.length) {
        return { ok: true };    
    }

    return { ok: false, error: `Glue is not initialized with { "appManager: "full" }. Missing props: ${missingAppManagerProps.join(", ")}` };
};

export const extractDecoderError = (decoderError: DecoderError) => {
    return `${decoderError.message} at ${decoderError.at} for input: ${JSON.stringify(decoderError.input)}`;
};
