import * as AgmNames from "./const/agm-names";
import { objectValues } from "./helper";
import AppManagerImpl from "./app-manager";
import { Configuration, GetApplicationsMethodResult } from "./types";
import { IOConnectDesktop } from "../../desktop";

/** Retrieves application snapshot from ACS */
export default function snapshot(interop: IOConnectDesktop.Interop.API, appManager: AppManagerImpl): Promise<Configuration> {
    return new Promise<Configuration>((resolve, reject) => {
        interop.invoke<GetApplicationsMethodResult>(AgmNames.GetApplicationsMethodName, { skipIcon: true })
            .then((response) => {
                const data = response.returned;
                const configuration = response.returned.configuration ?? {};
                if (!data) {
                    resolve(configuration);
                }

                const applications = data.applications;
                if (!applications) {
                    resolve(configuration);
                }

                objectValues(applications).map((item) => appManager.handleAppAdded(item));

                resolve(configuration);
            })
            .catch((err: Error) => reject(`Error getting application snapshot: ${err.message}`));
    });
}
