/* eslint-disable @typescript-eslint/no-explicit-any */

import { createFactoryFunction } from "./web";
import createIoConnectCore from "@interopio/core";
import { version } from "../package.json";

const iOConnectBrowserFactory = createFactoryFunction(createIoConnectCore);

// attach to window object
if (typeof window !== "undefined") {
    const windowAny = window as any;

    windowAny.IOBrowser = iOConnectBrowserFactory;

    delete windowAny.GlueCore;
    delete windowAny.IOConnectCore;
}

const legacyGlobal = (window as any).glue42gd || (window as any).glue42core;
const ioGlobal = (window as any).iodesktop || (window as any).iobrowser;

if (!legacyGlobal && !ioGlobal) {
    (window as any).iobrowser = { webStarted: false };
}

(iOConnectBrowserFactory as any).version = version;

export default iOConnectBrowserFactory;
